<script>
    import { swMenu } from "./store.js";

    import Home from "./Home.svelte";
    import Product from "./Product.svelte";
    import News from "./News.svelte";

    import Item01 from "./Item01.svelte";
    import Item02 from "./Item02.svelte";
    import Item03 from "./Item03.svelte";
    import Item04 from "./Item04.svelte";
    import Item05 from "./Item05.svelte";
    import Item06 from "./Item06.svelte";
    import Item07 from "./Item07.svelte";
    import ItemDtl from "./ItemDtl.svelte";

    import Contact from "./Contact.svelte";
    import About from "./About.svelte";

    import Map from "./Map.svelte";
    import MapMarker from "./MapMarker.svelte";

    let menu = 1;

    function menuHome() {
        swMenu.set(0);
    }
    function menuProduct() {
        swMenu.set(1);
    }
    function menuNews() {
        swMenu.set(2);
    }
    function menuContact() {
        swMenu.set(3);
    }
    function menuAbout() {
        swMenu.set(4);
    }
</script>

<div class="container">
    <div class="row">
        <div class="col">
            <img
                src="img/extrana.jpeg"
                class="img-fluid shadow"
                alt="extrana ~ cable."
            />
        </div>
    </div>
</div>

<div class="container mt-4">
    <div class="col-10 col-sm-10 col-md-10 col-lg-8">
        <div class="d-flex flex-row">
            <div>
                <img
                    src="img/menu/home.jpg"
                    alt="extrana ~ cable."
                    class="img-fluid"
                    on:click={menuHome}
                />
            </div>
            <div>
                <img
                    src="img/menu/product.jpg"
                    alt="extrana ~ cable."
                    class="img-fluid"
                    on:click={menuProduct}
                />
            </div>
            <div>
                <img
                    src="img/menu/news.jpg"
                    alt="extrana ~ cable."
                    class="img-fluid"
                    on:click={menuNews}
                />
            </div>
            <div>
                <img
                    src="img/menu/contact.jpg"
                    alt="extrana ~ cable."
                    class="img-fluid"
                    on:click={menuContact}
                />
            </div>
            <div>
                <img
                    src="img/menu/about.jpg"
                    alt="extrana ~ cable."
                    class="img-fluid"
                    on:click={menuAbout}
                />
            </div>
        </div>
    </div>
</div>

{#if $swMenu === 0}
    <Home />
{:else if $swMenu === 1}
    <Product />

{:else if $swMenu === 11}
    <Item01 />
{:else if $swMenu === 12}
    <Item02 />
{:else if $swMenu === 13}
    <Item03 />
{:else if $swMenu === 14}
    <Item04 />
{:else if $swMenu === 15}
    <Item05 />
{:else if $swMenu === 16}
    <Item06 />
{:else if $swMenu === 17}
    <Item07 />

{:else if $swMenu === 100}
    <ItemDtl />

{:else if $swMenu === 2}
    <News />
{:else if $swMenu === 3}
    <Contact />
{:else if $swMenu === 4}
    <About />
{:else}
    <h1 class="display-1 p-2">page not found ..</h1>
{/if}

{#if $swMenu === 4}
    <Map lat={-6.136622} lon={106.795184} zoom={15}>
        <MapMarker
            lat={-6.136622}
            lon={106.795184}
            label="united grand express"
        />
    </Map>
{/if}

<div style="height:50px;" />

<!-- FOOTER -->
<footer class="container pb-2">
    <p class="float-end"><a href="#">Back to top</a></p>
    <p>
        &copy; 2021 Extrana Cable, Inc. &middot; <a href="#">Privacy</a>
        &middot; <a href="#">Terms</a>
    </p>
</footer>
