        swItemDtl.set("");
        swMenu1.set(0);
    }
</script>

<div class="container">
    <div class="row">
        <div class="col">
            <img
                src="img/{$swItemDtl}"
                class="img-fluid"
                alt="extrana ~ cable."
            />
        </div>
    </div>
    <div class="ms-3">
        <button
            type="button"
            class="btn btn-outline-primary"
            on:click={menuBack}>Kembali</button
        >
    </div>
    <div class="border-bottom mt-5" />
</div>
