<script>
    import { swItemDtl, swMenu, swMenu1 } from "./store.js";

    let hdr = "";

    if ($swMenu === 11) {
        hdr = "High Voltage Cables";
    } else if ($swMenu === 12) {
        hdr = "Medium Voltage Cables";
    } else if ($swMenu === 13) {
        hdr = "Low Voltage Aluminium Cables";
    } else if ($swMenu === 14) {
        hdr = "Low Voltage Copper Cables";
    } else if ($swMenu === 15) {
        hdr = "Flexible Cables";
    } else if ($swMenu === 16) {
        hdr = "Overhead Conductors";
    } else if ($swMenu === 17) {
        hdr = "Instrument & Control";
    }

    function menuA() {
        swMenu1.set($swMenu);
        swItemDtl.set("item01/N2XCK2Y_NA2XCK2Y_01b.jpg");
        swMenu.set(100);
    }
    function menuB() {
        swMenu1.set($swMenu);
        swItemDtl.set("item01/N2XCK2Y_NA2XCK2Y_02b.jpg");
        swMenu.set(100);
    }
    function menuC() {
        swMenu1.set($swMenu);
        swItemDtl.set("item01/N2XCK2Y_NA2XCK2Y_03b.jpg");
        swMenu.set(100);
    }
    function menuD() {
        swMenu1.set($swMenu);
        swItemDtl.set("item01/N2XCK2Y_NA2XCK2Y_04b.jpg");
        swMenu.set(100);
    }

    function menuBack() {
        swMenu.set(1);
    }
</script>

<div class="container">
    <div class="row align-items-center border-bottom">
        <div class="col-5">
            <p class="display-6 text-muted">{hdr}</p>
        </div>
        <div class="col">
            <img
                src="img/item01/N2XCK2Y_NA2XCK2Y.jpg"
                class="img-fluid"
                alt="extrana ~ cable."
            />
        </div>
    </div>
</div>

<div class="container">
    <div class="row">
        <div class="col-6 col-sm-6 col-md-4 col-lg-4 col-xl-3 mb-4">
            <img
                src="img/item01/N2XCK2Y_NA2XCK2Y_01a.jpg"
                class="img-fluid shadow"
                alt="extrana ~ cable."
                on:click={menuA}
            />
        </div>

        <div class="col-6 col-sm-6 col-md-4 col-lg-4 col-xl-3 mb-4">
            <img
                src="img/item01/N2XCK2Y_NA2XCK2Y_02a.jpg"
                class="img-fluid shadow"
                alt="extrana ~ cable."
                on:click={menuB}
            />
        </div>
        <div class="col-6 col-sm-6 col-md-4 col-lg-4 col-xl-3 mb-4">
            <img
                src="img/item01/N2XCK2Y_NA2XCK2Y_03a.jpg"
                class="img-fluid shadow"
                alt="extrana ~ cable."
                on:click={menuC}
            />
        </div>
        <div class="col-6 col-sm-6 col-md-4 col-lg-4 col-xl-3 mb-4">
            <img
                src="img/item01/N2XCK2Y_NA2XCK2Y_04a.jpg"
                class="img-fluid shadow"
                alt="extrana ~ cable."
                on:click={menuD}
            />
        </div>
    </div>
    <div class="ms-3">
        <button
            type="button"
            class="btn btn-outline-primary"
            on:click={menuBack}>Kembali</button
        >
    </div>
</div>

<div class="container">
    <hr class="featurette-divider" />
</div>
